.contact{
    background-color: rgb(33, 20, 94);
    color: white;
    padding: 15px;
    display: flex;
    justify-content: center;

    .wrapper{
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        
        input{
            padding: 10px;
            border: none;
            border-radius: 5px 0 0 5px;
            outline: none;
        }
        button{
            padding: 10px;
            color: white;
            background-color: rgb(163, 150, 150);
            border: none;
            border-radius: 0 5px 5px 0;
        }
        .icons{
            display: flex;
            gap: 15px;
        }
    }
}